import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'checkattendence'
})
export class CheckattendencePipe implements PipeTransform {

  transform(value: any, args?: any): any {
    return value ? 'CheckedIn' : 'CheckedOut';
  }

}



